.login {
  background-color: #324b6b;
  height: 100vh;
  width: 100vw;
  display: flex;
}

.login-form {
  color: white;
  margin: auto;
  height: 35vh;
  width: 90%;
  box-sizing: border-box;
  background-color: #324b6b;
  border-radius: 8px;
}




.title {
  height: 32px;
  margin: 16px;
  color: white;
  text-align: center;
  background: rgba(255, 255, 255, 0.2);
  line-height: 32px;
  border-radius: 8px;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  letter-spacing: 1px;
}

body {
  background-color: rgb(255, 255, 255);
}

.ant-layout-content {
  position: relative;
}

.ant-layout-content>.content {
  position: absolute;
  width: 100%;
}

.ant-card-body {
  padding: 15px !important;
  /* height: 70% !important; */
}



/* 出场动画效果 */
.layout-enter {
  opacity: 0;
  /* transform: scale(0.5); */
  transform: rotate(180deg);
}

.layout-enter-active {
  opacity: 1;
  transition: all 300ms;
  transform: rotate(360deg);
  /* transform: scale(1); */
  /* 防止动画过程触摸屏幕引起的动画错乱 */
  touch-action: none;
}

/* .fds-enter-done {
  opacity: 1;
  transform: translate(0%);
} */


.layout-exit {
  opacity: 1;
  /* transform: scale(1); */
  transform: rotate(0deg);
}

.layout-exit-active {
  opacity: 0;
  transition: all 300ms;
  /* transform: scale(0.5); */
  transform: rotate(180deg);
  touch-action: none;
}

/* .fds-exit-done{
  transform: translate(-100%);
} */


.page {
  height: 100vh;
  overflow-y: scroll;
  width: 100vw;
  background-color: white;
  position: absolute;
  top: 0;
  left: 0;
}

.ant-layout {
  /* background-color: red !important; */
}

.top-search {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  margin: 20px 0;
}

.top-search>.city {
  font-size: 17px;
  font-weight: 600;
  width: 17%;
}

.top-search>.city>.arrow {
  display: inline-block;
  height: 0;
  width: 0;
  border: 6px solid black;
  border-left-color: white;
  border-bottom-color: white;
  border-right-color: white;
  margin-left: 3px;
}

.adm-tab-bar {
  position: absolute;
  bottom: 0;
  z-index: 1;
  width: 100%;
}

.home-banner {
  background-color: aqua;
  height: 200px;
  width: 100%;
}

.home-menu {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 10px 0;
}

.home-menu .menu-item {
  width: 25%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home-menu .menu-item span:first-child {
  display: inline-block;
  width: 50px;
  height: 50px;
  background-color: cyan;
  margin-bottom: 5px;
  border-radius: 40%;

}

.home-menu .menu-item span:last-child {
  font-weight: 600;
}

.home-card {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  padding: 10px 0;
}

.home-card>.adm-card {
  width: 45%;
  margin-bottom: 10px;
  position: relative;
}

.home-card>.adm-card span {
  /* float: right; */
  /* display: inline-block; */
  position: absolute;
  right: 10px;
  top: 10px;
  width: 50px;
  height: 50px;
  background-color: cyan;
  margin-bottom: 5px;
  border-radius: 40%;
}

/* 覆盖物样式 */
.custom {
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  background-color: #0088ff;
  border-radius: 50%;
  border: 2px solid #2373b8;
  color: white;
  font-size: 14px;
  font-weight: 600;
  box-shadow: 0 0 1 1 #47a9ff;
}
.adm-nav-bar{
  position: absolute;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 45px;
  background-color: white;
}