.ant-layout {
  height: 100vh;
}
*{
  padding: 0;
  margin: 0;
}
/* 出场动画效果 */
.fds-enter {
  transform: translate(100%);
}

.fds-enter-active {
  opacity: 1;
  transition: all 500ms;
  transform: translate(0%);
  /* 防止动画过程触摸屏幕引起的动画错乱 */
  touch-action: none; 
}

.fds-enter-done {
  opacity: 1;
  transform: translate(0%);
}

.fds-exit {
  opacity: 0;
  transform: translate(0%);
}

.fds-exit-active {
  opacity: 1;
  transition: all 500ms;
  transform: translate(-100%);
  touch-action: none;
}

.fds-exit-done{
  transform: translate(-100%);
}


/* 出场动画效果 */
.layout-enter {
  opacity: 0;
  /* transform: scale(0.5); */
  transform: rotate(180deg);
}

.layout-enter-active {
  opacity: 1;
  transition: all 300ms;
  transform: rotate(360deg);
  /* transform: scale(1); */
  /* 防止动画过程触摸屏幕引起的动画错乱 */
  touch-action: none;
}

/* .fds-enter-done {
  opacity: 1;
  transform: translate(0%);
} */


.layout-exit {
  opacity: 1;
  /* transform: scale(1); */
  transform: rotate(0deg);
}

.layout-exit-active {
  opacity: 0;
  transition: all 300ms;
  /* transform: scale(0.5); */
  transform: rotate(180deg);
  touch-action: none;
}

